.link{
    display: inline-flex;
    text-decoration: none;
    align-items: center;
    margin-top: 15px;
    font-weight: var(--font-bold);
    color:var(--color-purple)
}

.link__img{
    width: 30px;
    height: 30px;
    margin-right: var(--spacing-small);
    object-fit: cover;
    object-position: center center;
    fill: #ae72c2;
}